.container {
    margin: 3rem auto;
    max-width: 900px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #f5f5f5;
    color: #364f6b;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}

.container h2 {
    width: 100%;
    text-align: center;
}